body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

nav label {
  color: #fff;
}

.container-panel {
  position: relative;
  background-color: #fff;
  padding-top: 40px;
  margin-top: 20px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.5);
  padding: 20px 30px;
  font-family: 'Open sans', Arial, sans-serif;
  border-radius: 25px;
}

.container-panel {
  max-width: 60%;
  margin-left: auto;
  margin-right: auto;
}

.container-panel p {
  margin: 0px;
}

footer{
  width: 100%;
  height: 50px;
  background-color: gray;
  margin-top: 50px;
  color: white;
  text-align: center;
}

.paggination {
  margin-top: 50px;
  color: #fff;
}



.bg-dark a {
  color: white !important;
  padding-left: 50px;
  padding-right: 50px;
}

.nav-item .form-group {
  margin-bottom: 0px;
}
